<template>
    <b-col>
        <b-alert
            :variant="alertType"
            dismissible
            fade
            :show="dismissCountDown"
            @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"
        >
            <p>{{ alertMsg }}</p>
            <b-progress
                :variant="alertType"
                :max="dismissSecs"
                :value="dismissCountDown"
                height="4px"
            ></b-progress>
        
        </b-alert>

        <b-breadcrumb class="bg-white">
            <b-breadcrumb-item to="/">
            <b-icon icon="house-fill" scale="1.25" shift-v="1.25" aria-hidden="true"></b-icon>
             Home
            </b-breadcrumb-item>
            <b-breadcrumb-item active></b-breadcrumb-item>
        </b-breadcrumb>

        <b-card-group class="border shadow p-1 mb-5">
                <b-card class="border-0 overflow-auto">

                    <b-card
                        tag="article"
                        class="p-2 my-2 text-center">
                        <b-card-body>
                            <b-card-title class="text-center">
                                {{ form.title }}
                            </b-card-title>
                            <b-card-sub-title class="py-3 text-center">
                                <abbr><b-icon icon="clock-history"></b-icon> 
                                {{ date | moment('from') }}</abbr>
                            </b-card-sub-title>
                            <b-card-text class="py-3 text-left" v-html="form.content"></b-card-text>
                            <hr>
                            <!--  -->
                            <b-card-text v-if="uid === myid" >
                                <b-button @click="editArticle" id="pencil" variant="outline-white">
                                    <b-icon class="h4" icon="pencil" variant="primary"></b-icon>
                                </b-button>
                                <b-button @click="deleteArticle" id="trash" variant="outline-white">
                                    <b-icon class="h4" icon="trash" variant="primary"></b-icon>
                                </b-button>

                                <b-tooltip target="pencil" placement="topleft" variant="info"></b-tooltip>
                                <b-tooltip target="trash" placement="topright" variant="danger"></b-tooltip>
                            </b-card-text>
                        </b-card-body>
                        <!--  -->
                        <b-card-text>
                            <b-button-group>
                                <b-button @click="like" href="javascript:;" :class="likeClass" 
                                :variant="likeClass ? 'secondary' : 'info'">
                                    <i class="fa fa-thumbs-up"></i> {{ likeClass ? '' : '' }}
                                </b-button>
                                <div class="or"></div>
                                <!--  -->
                                <button @click="showQrcode" class="btn btn-success">
                                    <i class="fa fa-heart"></i> </button>
                            </b-button-group>
                        </b-card-text>
                        
                        <b-card-text class="mt-4 mb-0">
                            <div>
                                <span v-for="likeUser in likeUsers" :key="likeUser.uname">
                                    <router-link :to="`/${likeUser.uname}`" class="ml-1">
                                        <b-avatar class="animate__animated animate__bounce"
                                        variant="light" :src="likeUser.uavatar" alt="avatar" 
                                        ></b-avatar>
                                    </router-link>
                                </span>
                            </div>
                        </b-card-text>

                        <b-card-text>
                            <div v-if="!likeUsers.length" class="mt-3"> !</div>
                        </b-card-text>

                    </b-card>

                    <!--  -->
                    <b-modal ref="qrcode-modal" hide-footer>
                        <template v-slot:modal-title>
                            <b-avatar variant="success" :src="user.avatar" alt="avatar"></b-avatar>
                        </template>
                        <div class="d-block text-center">
                            <h5 class="mb-4"></h5>
                            <p>
                                <qrcode-vue name="dd" value="https://github.com/freecelestial/" 
                                :size="160"></qrcode-vue>
                            </p>
                        </div>
                    </b-modal>

                    <!--   -->
                    <b-card no-body>
                        <template v-slot:header>
                            <h5 class="mb-0">: <b>{{ comments.length }}</b></h5>
                        </template>
                        <b-list-group id="reply-list" name="fade" tag="transition-group" flush>
                            <b-list-group-item v-for="(comment, index) in comments" :key="comment.commentId">
                                <b-media tag="li">
                                    <template v-slot:aside>
                                        <router-link :to="`/${comment.uname}`">
                                            <b-avatar variant="info" :src="comment.uavatar" class="align-middle"></b-avatar>
                                        </router-link>
                                    </template>
                                    <h5 v-html="`#${index+1} <cite>${comment.uname}</cite>`" class="text-secondary"></h5>
                                    <h5 v-html="comment.content" class="my-1"></h5>

                                    <!--   -->
                                    <span v-if="comment.uid === myid" class="pull-right">
                                        <a @click="editComment(comment.commentId, index)" :id="`pencil2${index}`" href="javascript:;">
                                            <b-icon icon="pencil"></b-icon>
                                        </a>
                                        <a @click="deleteComment(comment.commentId)" :id="`trash2${index}`" href="javascript:;" >
                                            <b-icon icon="trash"></b-icon>
                                        </a>
                                        <b-tooltip :target="`pencil2${index}`" placement="topleft" variant="info"></b-tooltip>
                                        <b-tooltip :target="`trash2${index}`" placement="topright" variant="danger"></b-tooltip>
                                    </span>

                                    <p class="mb-0">
                                        <abbr class="text-secondary">{{ comment.date | moment('from', { startOf: 'second' }) }}</abbr>
                                    </p>

                                </b-media>
                            </b-list-group-item>
                        </b-list-group>
                    </b-card>

                    <!--   -->
                    <b-card-body>
                        <b-form>
                            <ValidationObserver ref="observer" v-slot="{ invalid }">
                            <ValidationProvider name="" 
                                    rules="required|max:300" v-slot="{ valid,errors }">
                                <b-form-group 
                                    label=": " 
                                >
                                    <b-form-textarea
                                        v-if="auth" 
                                        id="editor" 
                                        placeholder="" 
                                    ></b-form-textarea>

                                    <b-form-textarea
                                        v-else 
                                        disabled
                                        placeholder="" 
                                    ></b-form-textarea>
                                    <b-form-invalid-feedback id="inputLiveFeedback">
                                        {{ errors[0] }}</b-form-invalid-feedback>

                                </b-form-group>

                                <!-- commentHtml v-html  -->
                                <label v-show="commentHtml" >: </label>
                                <b-card v-show="commentHtml" class="bg-light">
                                    <div v-html="commentHtml"></div>
                                </b-card>


                                <b-button id="reply-btn" class="mt-4 text-center" :disabled="invalid" 
                                variant="primary" @click="comment">
                                    <b-icon icon="upload"></b-icon> 
                                    {{ commentId ? '' : '' }}
                                </b-button> 

                                <b-button v-show="commentId" class="mt-4 text-center" type="button" 
                                variant="outline-secondary"  @click="cancelEditComment">
                                    <b-icon icon="x"></b-icon> 
                                </b-button>
                                <span v-show="!commentId" class="help-inline">Ctrl+Enter</span>

                            </ValidationProvider>
                            </ValidationObserver>
                        </b-form>
                    </b-card-body>

                </b-card>
        </b-card-group>
            
    </b-col>
</template>

<script>
import SimpleMDE from 'simplemde'
import hljs from 'highlight.js'
import emoji from 'node-emoji'
import { mapState } from 'vuex'

// 
import { ValidationProvider, extend ,ValidationObserver} from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
import { messages } from 'vee-validate/dist/locale/zh_TW.json';

// rules
Object.keys(rules).forEach(rule => {
    extend(rule, {
         ...rules[rule], // copies rule configuration
         message: messages[rule] // assign message
    });
});

//  qrcode.vue 
import QrcodeVue from 'qrcode.vue'

export default {
    name: 'Content',
    data() {
        return {
            form:{
                title: '',
                content: ''
            },
            articleId: undefined,
            article:'',
            alertShow: null,
            alertMsg:'',
            alertType:'',
            dismissSecs: 5,
            dismissCountDown: 0,
            date: '', // 
            myid: 1, //  ID
            uid: 1, //  ID
            likeUsers: [], // 
            likeClass: '', // 
            commentHtml: '', //  HTML
            comments: [], // 
            commentId: undefined, //  ID
            commentMarkdown: ''
        }
    },
    computed: {
        ...mapState([
            'auth',
            'user'
        ])
    },
    created() {
        //  articleId
        const articleId = this.$route.params.articleId
        //  getArticleById  ID 
        const article = this.$store.getters.getArticleById(articleId)

        if (article) {

            let { uid, title, content, date, likeUsers, comments } = article

            this.uid = uid
            this.form.title = title
            //  markdown  Markdown  HTML
            // this.content = SimpleMDE.prototype.markdown(content)
            //  emojify  emoji name => name  
            this.form.content = SimpleMDE.prototype.markdown(emoji.emojify(content, name => name))
            this.date = date
            //  likeUsers
            this.likeUsers = likeUsers || []
            //  likeClass active
            this.likeClass = this.likeUsers.some(likeUser => likeUser.uid === this.myid) ? 'active' : ''

            //  comments
            this.renderComments(comments)

            this.$nextTick(() => {
                //  'pre code' 
                this.$el.querySelectorAll('pre code').forEach((el) => {
                    //  highlight.js  highlightBlock 
                    hljs.highlightBlock(el)
                })
            })
        }
        //  articleId
        this.articleId = articleId
    },
    mounted() {
        // 
        if (this.auth) {
            // 
            window.hljs = hljs

            const simplemde = new SimpleMDE({
                element: document.querySelector('#editor'),
                placeholder: ' Markdown ',
                spellChecker: false,
                autoDownloadFontAwesome: false,
                //  ( true)
                // toolbar: false,
                // 
                status: false,
                renderingConfig: {
                    codeSyntaxHighlighting: true
                }
            })

            // 
            simplemde.codemirror.on('change', () => {
                //  commentMarkdown 
                this.commentMarkdown = simplemde.value()
                //  commentHtml emoji  markdown  HTML
                this.commentHtml = simplemde.markdown(emoji.emojify(this.commentMarkdown, name => name))
            })

            //  Ctrl+Enter 
            simplemde.codemirror.on('keyup', (codemirror, event) => {
                if (event.ctrlKey && event.which === 13) {
                    this.comment()
                }else if (this.commentId && event.keyCode === 27) { 
                    //  commentId Esc 
                    // 
                    this.cancelEditComment()
                }
            })

            // 
            this.simplemde = simplemde
        }
    },
    methods: {
        showAlert(msg, type = 'success') {
            this.alertMsg = msg
            this.alertType = type
            this.alertShow = false
            this.$nextTick(() => {
                this.dismissCountDown = this.dismissSecs
                document.documentElement.scrollTop = 20
            })
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        editArticle() {
            // 
            this.$router.push({ name: 'Edit', params: { articleId: this.articleId } })
        },
        deleteArticle() {
            this.$bvModal.msgBoxConfirm('?', {
                size: 'sm',
                buttonSize: 'sm',
                okVariant: 'danger',
                okTitle: '',
                cancelTitle: '',
                footerClass: 'p-2',
                hideHeaderClose: false,
                centered: true
            })
            .then(value => {
                if (value) {
                    this.$store.dispatch('post', { articleId: this.articleId })
                }

            })
            .catch(err => {
               alert('!')
               
            })
        },
        like(evt) {
            evt.preventDefault()
            if (!this.auth) {
                this.showAlert('','warning')

            } else {
                const target = evt.currentTarget
                //  like
                const active = target.classList.contains('active')
                const articleId = this.articleId
                if (active) {
                    // 
                    //  like 
                    this.likeClass = ''
                    //  like  likeUsers 
                    //  store.dispatch  Promise 
                    // 
                    //  likeUsers
                    this.$store.dispatch('like', { articleId }).then((likeUsers) => {
                        // 
                        // this.likeUsers = likeUsers
                        // 
                        this.likeUsers = this.recompute('likeUsers')
                    })
                } else {
                    // 
                    this.likeClass = 'active animate__animated animate__rubberBand'
                    //  like  isAdd:true 
                    this.$store.dispatch('like', { articleId, isAdd: true }).then((likeUsers) => {
                        // this.likeUsers = likeUsers
                        // 
                        this.likeUsers = this.recompute('likeUsers')

                        // 
                        // store  likeUsers
                        if(Array.isArray(this.likeUsers) && this.likeUsers.length == 0){
                            this.likeUsers.push({uid:this.myid,
                                                uname:this.user.name,
                                                uavatar:this.user.avatar })

                        }

                    })
                }
                
            }
        },
        comment() {
            // 
            if (this.commentMarkdown && this.commentMarkdown.trim() !== '') {
                //  comment 
                this.$store.dispatch('comment', 
                    {articleId: this.articleId,
                    comment: { content: this.commentMarkdown },
                    //  commentId 
                    commentId: this.commentId
                }).then(this.renderComments)
                //  .then  this.renderComments 

                //  commentId 
                if (this.commentId) {
                    this.cancelEditComment()
                } else {
                    // 
                    this.simplemde.value('')
                    // 
                    document.querySelector('#reply-btn').focus()
                    // 
                    this.$nextTick(() => {
                        const lastComment = document.querySelector('#reply-list .list-group-item:last-child')
                        if (lastComment) lastComment.scrollIntoView(true)
                        // 20 px
                        window.scrollBy(0,-20)
                    })
                }

            }
        },
        renderComments(comments) {
            if (Array.isArray(comments)) {
                // 
                comments = this.recompute('comments')

                //  comments 
                const newComments = comments.map(comment => ({ ...comment }))
                const user = this.user || {}

                for (let comment of newComments) {
                    //  Markdown  HTML
                    comment.content = SimpleMDE.prototype.markdown(emoji.emojify(comment.content, name => name))
                }

                //  comments
                this.comments = newComments
                //  Markdown 
                this.commentsMarkdown = comments
            }
        },
        editComment(commentId, commentIndex) {
            // 
            const simplemde = this.simplemde
            // 
            const codemirror = simplemde.codemirror
            // Markdown 
            const comments = this.commentsMarkdown

            for (const comment of comments) {
                //  commentId 
                if (parseInt(comment.commentId) === parseInt(commentId)) {
                    // 
                    simplemde.value(comment.content)
                    // 
                    codemirror.focus()
                    // 
                    codemirror.setCursor(codemirror.lineCount(), 0)
                    //  + 1
                    this.commentIndex = commentIndex + 1
                    //  commentId
                    this.commentId = commentId
                    break
                }
            }
        },
        // 
        cancelEditComment() {
            //  commentId
            this.commentId = undefined
            // 
            this.simplemde.value('')

            //  DOM 
            this.$nextTick(() => {
                if (this.commentIndex === undefined) return
                // nth-child  1 
                const currentComment = document.querySelector(`#reply-list .list-group-item:nth-child(${this.commentIndex})`)

                if (currentComment) {
                    currentComment.scrollIntoView(true)
                    window.scrollBy(0,-60)
                    // currentComment.querySelector('.operate a').focus()
                }
            })
        },
        // 
        deleteComment(commentId) {
            this.$bvModal.msgBoxConfirm('?', {
                size: 'sm',
                buttonSize: 'sm',
                okVariant: 'danger',
                okTitle: '',
                cancelTitle: '',
                footerClass: 'p-2',
                hideHeaderClose: false,
                centered: true
            })
            .then(value => {
                if (value) {
                    this.$store.dispatch('comment', {
                        commentId,
                        articleId: this.articleId
                    }).then(this.renderComments)

                    this.cancelEditComment()
                }

            })
            .catch(err => {
               alert('!')
               
            })

        },
        // ()
        recompute(key) {
            const articleId = this.$route.params.articleId
            //  getters.computedArticles 
            const article = this.$store.getters.getArticleById(articleId)

            let arr
            if (article) {
                arr = article[key]
            }

            return arr || []
        },
        showQrcode(){
            if (!this.auth) {
                this.showAlert('','warning')
            } else {
                this.$refs['qrcode-modal'].show()
            }
        }
    },
    components: {
        ValidationProvider,ValidationObserver,QrcodeVue
    }
}
</script>

<style lang="scss" >
    .fade-enter-active, .fade-leave-active { transition: opacity .5s;}
    .fade-enter, .fade-leave-to { opacity: 0;}

    .CodeMirror,.CodeMirror-scroll {
        min-height: 100px;
    }
    .or {
        position: relative;
        float: left;
        width: .9em;
        height: 2.57142em;
        z-index: 3;
    }
    .or:before {
        position: absolute;
        text-align: center;
        border-radius: 500rem;
        content: "or";
        top: 50%;
        left: 50%;
        background-color: #fff;
        text-shadow: none;
        margin-top: -.922855em;
        margin-left: -.802855em;
        width: 1.78571em;
        height: 1.78571em;
        line-height: 1.78571em;
        color: rgba(0,0,0,.4);
        font-style: normal;
        font-weight: 700;
        -webkit-box-shadow: 0 0 0 1px transparent inset;
        box-shadow: inset 0 0 0 1px transparent;
    }
    .help-inline {
        margin-left: 10px;
        vertical-align: bottom;
    }

</style>


